.wrapper {
  margin-top: 1rem; }
  @media (max-width: 767px) {
    .wrapper {
      margin-left: 1rem;
      margin-right: 1rem; } }
  .wrapper + .wrapper {
    margin-top: 1rem; }
  .wrapper .label {
    font-size: 1rem;
    color: #868e96;
    margin-bottom: 0.25rem; }
  .wrapper .input {
    width: 100%;
    border: 1px solid #dee2e6;
    outline: none;
    border-radius: 0px;
    line-height: 2.5rem;
    font-size: 1.2rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    height: 2.8rem; }



